.tree li {
  margin: 0 0 !important;
  list-style-type: none;
  position: relative;
  padding: 20px 5px 0px 5px; }
  .tree li small {
    display: inline-block; }

.tree li::before {
  content: "";
  position: absolute;
  top: 0;
  width: 1px;
  height: 100%;
  right: auto;
  left: -20px;
  border-left: 1px solid #ccc;
  bottom: 50px; }

.tree li::after {
  content: "";
  position: absolute;
  top: 30px;
  width: 25px;
  height: 20px;
  right: auto;
  left: -20px;
  border-top: 1px solid #ccc; }

.tree li a[href="#"] {
  display: inline-block;
  vertical-align: top;
  padding: 5px 10px;
  text-decoration: none;
  color: #666;
  font-family: arial, verdana, tahoma;
  font-size: 11px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px; }
  .tree li a[href="#"].folder {
    background: #ffe79c;
    border: 1px solid #ffc203; }
  .tree li a[href="#"] ~ ul li a.folder ~ ul li a.folder {
    background: #fff3cf;
    border: 1px solid #ffdb69; }
    .tree li a[href="#"] ~ ul li a.folder ~ ul li a.folder ~ ul li a.folder {
      background: #fffdf8;
      border: 1px solid #ffe79c; }
  .tree li a[href="#"].file {
    background: #b1eef6;
    border: 1px solid #0095a8; }
  .tree li a[href="#"] ~ ul li a.folder ~ ul li a.folder ~ ul li a.file {
    background: #f6fdfe;
    border: 1px solid #38e8ff; }

/*Remove connectors before root*/
.tree > ul > li::before,
.tree > ul > li::after {
  border: 0; }

/*Remove connectors after last child*/
.tree li:last-child::before {
  height: 30px; }

/*Time for some hover effects*/
/*We will apply the hover effect the the lineage of the element also*/
.tree li a:hover,
.tree li a:hover + ul li a {
  color: #000; }
  .tree li a:hover.folder,
  .tree li a:hover + ul li a.folder {
    background: #ffdb69;
    border: 1px solid #cf9d00; }
  .tree li a:hover.file,
  .tree li a:hover + ul li a.file {
    background: #83e5f1;
    border: 1px solid #006875; }

/*Connector styles on hover*/
.tree li a:hover + ul li::after,
.tree li a:hover + ul li::before,
.tree li a:hover + ul::before,
.tree li a:hover + ul ul::before {
  border-color: #94a0b4; }

